.slider-section {
    @media screen and (min-width: 1000px) {
            padding-top: 353px;
            padding-bottom: 229px;
        }
    
        @media screen and (min-width: 768px) and (max-width: 999px) {
            padding-top: 279px;
            padding-bottom: 197px;
        }
    
        @media screen and (max-width: 767px) {
            padding-top: 154px;
            padding-bottom: 170px;
        }
}
.slider-person {
    width: 85px;
    height: 85px;
    margin-left: auto;
    margin-right: auto;

    @media screen and (min-width: 1000px) {
        margin-bottom: 15px;
    }
    @media screen and (min-width: 768px) and (max-width: 999px){
        margin-bottom: 19px;
    }
    @media screen and (max-width: 767px){
        margin-bottom: 24px;
    }    

}

.slider-text {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: auto;
    margin-right: auto;
    background-color: #FAFAFA;

    @media screen and (min-width: 1000px) {
        width: 926px;
        height: 138px;
        
        margin-bottom: 10px;
        img{
    width: 822px;
        }
    }
    @media screen and (min-width: 768px) and (max-width: 999px){
        width: 620px;
        height: 164px;

        margin-bottom: 24px;
        img{
            width: 544px;
        }
    }
    @media screen and (max-width: 767px){
        width: 280px;
        height: 308px;

        margin-bottom: 35px;
        img{
            width: 180px;
        }
    }    

}
.slider-dots {
    width: 32px;
    margin-left: auto;
    margin-right: auto;

        @media screen and (min-width: 1000px) {
                margin-bottom: 20px;
            }
        
            @media screen and (min-width: 768px) and (max-width: 999px) {
                margin-bottom: 14px;
            }
        
            @media screen and (max-width: 767px) {
                margin-bottom: 18px;
            }
}

.slider-adress {
        font-family: 'DM Sans';
        font-style: normal;
        font-weight: 500;
        line-height: 1.86;
        text-align: center;
    
        color: #000000;
    @media screen and (min-width: 1000px) {
    font-size: 20px;
    margin-bottom: 35px;
    }
    @media screen and (min-width: 768px) and (max-width: 999px){
    font-size: 16px;
    margin-bottom: 35px;
    }
    @media screen and (max-width: 767px){
    font-size: 14px;
    margin-bottom: 30px;
    }    
}

 .slick-slider {
     position: relative;
 }

 .slick-list {
     overflow: hidden;
 }

 .slick-dots li {
    background-image: url(../images/people/Ellipse.svg);
    background-size: 10px;
    background-position: center;
    background-repeat: no-repeat;
 }
 .slick-dots li button:not(:last-child){
     margin-right: 18px;
 }
.slick-dots li button::before{
    display: none;
}
//  /* Активная точка */
 .slick-dots li.slick-active {
    background-image: url(../images/people/home.png);
    background-size: 10px;
    background-repeat: no-repeat;
    background-position: center;
 }