.gallery {
  position: relative;

  margin: 0 auto;
  z-index: 2;
}

.gallery .gallety-box {
  position: relative;
  top: -47px;
  left: 50%;
  transform: translateX(-50%);
}

.gallery-list {
  position: absolute;
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  grid-template-rows: repeat(3, 1fr);
  gap: 6px;
}

.gallety-list__photo {
  position: relative;
  border-radius: 1.56vw;
  transform: scale(1);

  transition: transform 250ms linear;

  &:hover,
  &:focus {
    cursor: pointer;
    z-index: 10;
    transform: scale(1.15);
    box-shadow: 0 0 5px 10px rgb(0 0 0 / 10%);
  }
}

.gallery-list:hover .gallety-list__photo:not(:hover) {
  filter: grayscale(100%);
}

.gallery-list__element {
  margin: 0 auto;
}

.gallery-list__element:nth-child(1) {
  grid-column: 1;
  grid-row: 2;
}
.gallery-list__element:nth-child(2) {
  margin-top: 65%;
  grid-column: 2;
  grid-row: 1/3;
}
.gallery-list__element:nth-child(3) {
  grid-column: 3/5;
  grid-row: 1/3;
}
.gallery-list__element:nth-child(4) {
  margin-top: 75%;
  grid-column: 5;
  grid-row: 1/3;
}
.gallery-list__element:nth-child(5) {
  margin-top: 30%;
  grid-column: 6;
  grid-row: 1/3;
}
.gallery-list__element:nth-child(6) {
  grid-column: 7;
  grid-row: 2;
}
.gallery-list__element:nth-child(7) {
  margin-top: 65%;
  grid-column-start: 2;
  grid-row: 2/4;
}
.gallery-list__element:nth-child(8) {
  grid-column: 3;
  grid-row: 3;
}
.gallery-list__element:nth-child(9) {
  margin-top: 75%;
  grid-column: 5;
  grid-row: 2/4;
}
.gallery-list__element:nth-child(10) {
  margin-top: 30%;
  grid-column: 6;
  grid-row: 2/4;
}

@media screen and (max-width: 767px) {
  .button-gallery {
    display: none;
  }
}

@media screen and (min-width: 768px) {
  .gallery .gallety-box {
    // top: -45vw;
    top: -109px;
  }
}

@media screen and (min-width: 1000px) {
  .gallery .gallety-box {
    top: -171px;
  }

  .button-gallery {
    display: block;
    margin: 0 auto;
    margin-top: 430px;
  }
}
