body {
  width: 100%;
}

.none {
  display: none;
}

.hidden, body.modal-open {
  overflow: hidden;
}

a {
  text-decoration: none;
}

a:focus {
  outline: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

svg {
  fill: currentColor;
}

input {
  margin: 0;
  padding: 0;
}

button {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  margin: 0;
  padding: 0;
  display: inline-block;
}

button:focus {
  outline: none;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.section {
  margin: 0 auto;
}

.is-open {
  opacity: 0;
}

.conteiner {
  min-width: 280px;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 480px) and (max-width: 767px) {
  .conteiner {
    width: 468px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1080px) {
  .conteiner {
    width: 768px;
  }
}

@media screen and (min-width: 1081px) {
  .conteiner {
    width: 1081px;
  }
}

:root {
  --focus-pinc-color: #d41443;
  --accent-salmon-color: #ffa5ba;
  --text-black-color: black;
  --text-gray-color: #907e82;
  --focus-white-color: #fff;
  --main-transition: 250ms cubic-bezier(.4, 0, .2, 1);
}

.header {
  transition: background-color .25s linear;
}

.header-box {
  align-items: center;
  padding-top: 6px;
  display: flex;
}

.logo {
  color: #d41443;
  align-items: center;
  font-family: Titan One;
  font-size: 26px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.15385;
  display: flex;
}

.logo__photo {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin-right: 20px;
}

.header__menu {
  z-index: 5;
  margin-left: auto;
  position: relative;
}

.header__dutton-end {
  z-index: 1001;
  position: absolute;
  top: 23px;
  right: 28px;
}

.header__button {
  color: #d41443;
  border-radius: 22px;
  font-family: DM Sans;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.3125;
}

.header__button svg {
  margin-left: 24px;
}

.header__button:hover, .header__button:focus {
  box-shadow: 0 3px 1px rgba(0, 0, 0, .1), 0 1px 2px rgba(0, 0, 0, .08), 0 2px 2px rgba(0, 0, 0, .12);
}

.header__button-two {
  width: 100%;
  height: 44px;
  background-color: #fff;
  margin-bottom: 32px;
}

.nav-list {
  gap: 22px;
  display: grid;
}

.nav-list__link {
  z-index: 5;
  color: #fff;
  font-family: DM Sans;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.28571;
  transition: color .25s linear;
  position: relative;
}

.nav-list__link:hover, .nav-list__link:focus, .nav-list__link--color {
  color: #d41443;
}

.nav-list__link-decoration-r {
  color: #d41443;
  opacity: 0;
  transition: transform .25s linear, opacity .25s linear;
  display: block;
  position: absolute;
  top: 0;
  right: -10px;
  transform: translate(25px);
}

.nav-list__link:hover .nav-list__link-decoration-r, .nav-list__link:focus .nav-list__link-decoration-r {
  opacity: 1;
  transform: translate(0);
}

.nav-list__link-decoration-l {
  color: #d41443;
  opacity: 0;
  transition: transform .25s linear, opacity .25s linear;
  display: block;
  position: absolute;
  top: 0;
  left: -10px;
  transform: translate(-25px);
}

.nav-list__link:hover .nav-list__link-decoration-l, .nav-list__link:focus .nav-list__link-decoration-l {
  opacity: 1;
  transform: translate(0);
}

@media screen and (max-width: 767px) {
  .header__button-one {
    display: none;
  }
}

@media screen and (max-width: 999px) {
  .header {
    background-color: #ffb8ca;
  }

  .nav-box {
    padding: 60px 21px;
  }

  .nav {
    z-index: 1000;
    width: 250px;
    height: 100%;
    opacity: 0;
    background-color: #e17992;
    transition: transform .25s linear, opacity .25s linear;
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    transform: translate(100%);
  }

  .nav--show {
    opacity: 1;
    transform: translate(0);
  }
}

